}
.category-icon:last-child {
  margin-right: 0px;
}
.relative{
  position: relative;
}
.move {
  left: -370px;
  animation: move 1s;
  -webkit-animation: move 1s; /*Safari and Chrome*/
}
.leave {
  animation: leave 1s;
  -webkit-animation: leave 1s; /*Safari and Chrome*/
}
.icon {
  font-size: 42px;
}
.active {
  color: #ff8888;
  border-color: #ff8888;
}
@keyframes move {
from {
    left: -0px;
}
to {
    left: -370px;
}
}
@-webkit-keyframes move {
from {
    left: -0px;
}
to {
    left: -370px;
}
}
@keyframes leave {
from {
    left: -370px;
}
to {
    left: 0px;
}
}
@-webkit-keyframes leave {
from {
    left: -370px;
}
to {
    left: 0px;
}
}
